import React from 'react';
import { string } from 'prop-types';

const renderIcon = (image) => {
  if (image) {
    try {
      let src = image;
      if (image.includes('.')) {
        src = require(`./${image}`); // eslint-disable-line
      } else {
        src = require(`./${image}.svg`); // eslint-disable-line
      }
      
      return (
        <img src={src} alt={image} />
      );
    } catch (e) {
      console.log(e);
    }
  }
  return null;
};

const Icon = ({ image }) => (
  image ?
    renderIcon(image)
  :
    null
);

Icon.propTypes = {
  image: string,
};

Icon.defaultProps = {
  image: '',
};

export default Icon;



