import React from 'react';
import { arrayOf, string, shape, object } from 'react-proptypes';

import './TrustBlock.scss';

const TrustBlock = ({ params }) => (
    <div className="trustBlock__container">
        <h4>
            {params.title}
        </h4>

        {/* <div className="trustBlock__items">
            {
                params.items.map((item, index) => (
                    <div key={index}>
                        <img src={`./logos/${item.image}`} />
                    </div>)
                )
            }
        </div> */}

        <div className="trustBlock__items">
            {
                params.items.map((item, index) => (
                    <div key={index}
                        style={{backgroundImage: `url(./logos/${item.image})`}}>
                    </div>)
                )
            }
        </div>

        <div className="divider"/>
    </div>
);

TrustBlock.propTypes = {
    params: shape({
        title: string,
        items: arrayOf(object),
    })
};

export default TrustBlock;



